* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  background: radial-gradient(circle, transparent 2px, rgba(1, 1, 1, 0.1) 1px) 20px 20px / 30px 30px;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  outline: none;
  border-color: navy;
  transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
  border-radius: 5px;
}
#search-box:active,
#search-box:hover,
#search-box:focus {
  box-shadow: 0 0 0 2px navy;
}
.country-list {
  list-style: none;
  padding: 0;
}

.country-icon {
  width: 50px;
  margin-right: 10px;
}

.country-title {
  font-size: 2em;
  font-weight: 700;
}
